import React from 'react'
import errorImage from "../assets/images/placeholder.png"
import './style.css'
import { thumbImages } from '../constants/pages';
const GridItem = ({ image, title }) => {
    return (
        <span className='grid-item'>
            <img height={220} src={thumbImages[image] || errorImage} onError={({ currentTarget }) => {
                currentTarget.onerror = null; // prevents looping
                currentTarget.src = errorImage;
            }} />
            <span>{title || "Test1"}</span>
        </span>
    )
}
export default React.memo(GridItem)